import './Conocenos.scss'
import banner1 from '../recursos/imprenta-digital.jpg'
import banner2 from '../recursos/impresora.jpg'
import banner3 from '../recursos/impresora-2.jpg'
import banner4 from '../recursos/nuestra-grafica.jpg'
import RubberBand from 'react-reveal/RubberBand'
import { Slide, Zoom } from 'react-reveal'

function Conocenos() {
    return (  
        <div className="about">
            <div className='info-gral'>
                <RubberBand><h3>Conocenos un poco mas...</h3></RubberBand>
                <div className="info-der">
                    <Zoom><img className='img-xl' loading='lazy' src={banner1} alt='print,grafica' /></Zoom>
                    <div className='text'>
                        <Zoom left cascade><h4>Bienvenidos a Grafica F y C</h4></Zoom>                    
                        <p><i><Zoom bottom cascade>
                        Somos un proyecto familiar enfocado principalmente en la publicidad visual a 
                        travs de la cartelera, folletera y publicidad digital, en la cual usted 
                        puede encontrar un beneficio por el cual incrementar su trascendencia productiva 
                        o de ventas. Ofrecemos calidad y rapidez en todas nuestras reas de trabajo siempre 
                        teniendo en cuenta la necesidad de cada cliente. Como puede notar en nuestra web podr 
                        encontrar todos nuestros trabajos realizados hasta la fecha, brindando garanta en la 
                        labor descripta anteriormente. 
                        </Zoom></i></p>                       
                    </div>
                    <Slide bottom><img className='img-xl' loading='lazy' src={banner3} alt='print,grafica' /></Slide>
                </div>
                <div className="info-cruzada">
                    <Slide left><img className='img-m' loading='lazy' src={banner2} alt='print,grafica' /></Slide>
                    <div className='texto'>
                        <Zoom right cascade><h4>Produccin de calidad.</h4></Zoom>                        
                        <p><i><Zoom right cascade>
                        Contamos con maquinas de ultimo rodaje en el pas, 
                        pudiendo as brindar una calidad de impresin ntida 
                        y con colores llamativos en la cual llamar la atencin 
                        de cualquier persona que lo vea, nuestros materiales 
                        cuentan con verificacin de calidad HP 
                        (una de las mejores marcas mundiales si se habla de impresin). 
                        Todos nuestros diseos son personalizados, es decir no volver a 
                        encontrar un trabajo copia del suyo, usted tendr una marca nica. 
                        </Zoom></i></p>                        
                    </div>
                </div>
                <div className="info-cruzada1">
                    <Slide right><img className='img-m1' loading='lazy' src={banner4} alt='print,grafica' /></Slide>
                    <div className='texto1'>
                        <Zoom left cascade><h4>Servicio a su disposicin.</h4></Zoom>                        
                        <p><i><Zoom left cascade>
                        Aunque contamos con atencin al pblico, 
                        aportamos un servicio sin costo extra de visita a su locacin para su comodidad. 
                        Esto tambin nos beneficia en sentido de trabajo ya que podremos 
                        ver con claridad la zona donde procesaremos la cartelera. 
                        No dude en contactarnos ya que esto podra ser una oportunidad de 
                        renovar su negocio y darle una nueva imagen al pblico, los tiempos 
                        van cambiando y siempre se necesita estar al margen del presente.
                        </Zoom></i></p>                        
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Conocenos;